.em-chat-content-item {
    .textcomplete-container {
        position: relative;
        .append-to {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
        }
    }
    .ui.comments &.comment {
        >.avatar~.content {
            margin-left: 3em;
        }
        .actions {
            > a {
                margin-right: 5px;
            }
        }
    }
    > .content {
        > .markdown-body {
            span.at-user {
                cursor: pointer;
            }
        }
    }
}
